import React from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlusCircle } from "@fortawesome/free-solid-svg-icons";

export default function MyBookmarksByCategory({ item, showBookmarkModal }) {
  return (
    <h6 style={{ lineHeight: '1.7rem' }}>
      <a href="/bookmarks/2/1223" data-category="1223" title=" ">
        <strong>{item.categoryName}</strong>
      </a>
      &nbsp;&nbsp;
      <span className="align-middle" title=" ">
        <FontAwesomeIcon
          icon={faPlusCircle}
          size="lg"
          onClick={(e) => showBookmarkModal(null)}
        />
      </span>
    </h6>
  )
}
