/* eslint-disable no-script-url */
/* eslint-disable eqeqeq */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react'
import "../css/browser_setting.css";

export default function BrowserSetting() {
    const menus = [
        { id: 1, title: '   ', name: 'chromePcStart' },
        { id: 2, title: '   ', name: 'chromeMobileStart' },
        { id: 3, title: '   ', name: 'chromePcBookmark' },
        { id: 4, title: '   ', name: 'chromeMobileBookmark' }
    ];

    const [curMenuId, setCurMenuId] = React.useState('1');
    const [curMenuTitle, setCurMenuTitle] = React.useState('   ');

    const handleMenuClick = (e) => {
        setCurMenuId(e.target.getAttribute('menu-id'));
        setCurMenuTitle(e.target.outerText);
        Title();
        Content();
    }

    const Title = () => {
        return (
            <>
                <ul className='nav nav-pills nav-fill bg-light'>
                    {menus.map((m) => (
                        <li key={m.id} className="nav-item">
                            <a href="javascript:;" className={(m.id == curMenuId) ? "bg-warning text-secondary nav-link" : "nav-link"}
                                onClick={handleMenuClick}
                                menu-id={m.id}>
                                {m.title}</a>
                        </li>
                    ))}
                </ul>
                <h2>{curMenuTitle}</h2>
            </>
        )
    }

    const Content = () => {
        let htmlContent = '';
        if (curMenuId == 2) {
            htmlContent = `<li><p>1.     </p><img src="/img/browser_setting/mobile_chrome_st/1.jpg"></li>
            <li><p>2.  </p><img src="/img/browser_setting/mobile_chrome_st/2.jpg"></li>
            <li><p>3.   </p><img src="/img/browser_setting/mobile_chrome_st/3.jpg"></li>
            <li><p>4.     http://startmypage.com </p><img src="/img/browser_setting/mobile_chrome_st/4.jpg"></li>`;
        } else if (curMenuId == 3) {
            htmlContent = `<li><p>   <br>    .</p><img src="/img/browser_setting/pc_chrome_fa/1.png"></li>`;
        } else if (curMenuId == 4) {
            htmlContent = `<li><p>1.       </p><img src="/img/browser_setting/mobile_chrome_fa/1.jpg"></li>
            <li><p>2.     .</p><img src="/img/browser_setting/mobile_chrome_fa/2.jpg"></li>`;
        } else {
            htmlContent = `<li>
                    <p>1.      </p>
                    <img src="/img/browser_setting/pc_chrome_st/1.png" alt=" " />
                </li>
                <li>
                    <p>2.  </p>
                    <img src="/img/browser_setting/pc_chrome_st/2.png" alt=" " />
                </li>
                <li>
                    <p>3.      </p>
                    <img src="/img/browser_setting/pc_chrome_st/3.png" alt=" " />
                </li>
                <li>
                    <p>4.       </p>
                    <img src="/img/browser_setting/pc_chrome_st/4.png" alt=" " />
                </li>
                <li>
                    <p>5. http://startmypage.com   </p>
                    <img src="/img/browser_setting/pc_chrome_st/5.png" alt=" " />
                </li>`;
        }

        return (
            <>
                <ul className="container" id="contentList" dangerouslySetInnerHTML={{ __html: htmlContent }}></ul>
            </>
        )
    }

    return (
        <section className="container-xl">
            <div className='col-lg-9' id="mainWrap">
                <Title />
                <Content />
            </div>
        </section>
    );
}
